<template>
  	<div>
  		<div class="menu-icon-content">
  			<div class="menu-icon">
				<span class="menu-icon__line menu-icon__line-left"></span>
				<span class="menu-icon__line"></span>
				<span class="menu-icon__line menu-icon__line-right"></span>
			</div>
  		</div>

		<div class="nav-responsive">
			<div class="nav__content">
				<ul class="nav__list">
					<li class="nav__list-item">
						<router-link :to="{name: 'home'}" @click="hideNavResponsive">
							<span v-show="isEn">About Me</span>
							<span v-show="isEs">Sobre Mi</span>
						</router-link>
					</li>
				</ul>
			</div>
		</div>


  		<transition
	      name="other-classes-transition"
	      mode="out-in"
	      enter-active-class="animated bounceInUp"
	      leave-active-class="animated bounceOutDown">
	      <router-view></router-view>
	    </transition>
	  		
  		<nav>
			<ul class="cd-vertical-nav">
				<li v-if="prevPage != null">
					<router-link :to="{name: prevPage}" class="cd-prev">Prev</router-link>
				</li>
				<li v-if="nextPage != null">
					<router-link :to="{name: nextPage}" class="cd-next">Next</router-link>
				</li>
			</ul>
		</nav> <!-- .cd-vertical-nav -->
  	</div>
</template>

<script>
	export default {
		data() {
			return {
				prevPage: null,
				nextPage: null,
			}
		},
	    mounted() {
	    	this.currentRoute();

	    	let body;
		  	let menu;
		  	let menuItems;

		  	body = document.querySelector("body");
		    menu = document.querySelector(".menu-icon");
		    menuItems = document.querySelectorAll(".nav__list-item");

		    this.applyListeners(menu, body, "nav-active");
	    },
	    updated() {
	    	this.currentRoute();
	    },
	    methods: {
	    	currentRoute: function() {
	    		var currentRoute = this.$router.currentRoute.name;
	    		if(!isNaN(currentRoute)) 
	    		{
	    			currentRoute = parseInt(currentRoute);
	    			if(currentRoute > 1) 
			    		this.prevPage = String(currentRoute - 1);
		    		else
		    			this.prevPage = null;

		    		if(currentRoute < 2) 
			    		this.nextPage = String(currentRoute + 1);
		    		else
		    			this.nextPage = null;
	    		}
	    		else
	    		{
	    			this.prevPage = null;
					this.nextPage = null;
	    		}
	    	},
	    	applyListeners: function(menu, body, classes) {
			    menu.addEventListener("click", () => this.toggleClass(body, classes));
		  	},
			toggleClass: function(element, stringClass) {
			    if (element.classList.contains(stringClass))
			    	element.classList.remove(stringClass);
			    else
			    	element.classList.add(stringClass);
		  	},
		  	hideNavResponsive: function() {
		  		document.querySelector("body").classList.remove("nav-active");
		  	}
	    },
	    computed: {
	      	isEs: function() {
	        	return this.$store.state.language == 'es';
	      	},
	      	isEn: function() {
	        	return this.$store.state.language == 'en';
	      	}
	    },
	}
</script>
