<template>
  <div v-bind:class="{ 'img-content': !loaded }" v-bind:style="{width: width, height: height, 'min-height': minHeight}">
    <img v-bind:src="imageSrc" alt="" width="100%" class="animated fadeIn" @load="onLoaded">
  </div>
</template>

<script>
export default {
  name: 'ImgContent',
  props: ['imageSrc', 'width', 'height', 'minHeight'],
  data() {
    return {
      loaded: false
    }
  },
  methods: {
    onLoaded() {
      this.loaded = true;
    }
  }
}
</script>